
ul,li {
  list-style: none;
  margin: 0;
  padding: 0;
}
body {
  background: #000;
}
#app {
  font-size: 20px;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  width: 60%;
  color: #2c3e50;
  margin: 60px auto;
}
.data {
  font-size: 16px;
  width: 100%;
  margin-top: 10px;
  text-align: center;
  color: #fff;
}
#app .event {
  color: #fff;
  margin-bottom: 10px;
}
#app .event .clock {
  display: inline-block;
  width: 15px;
  height: 15px;
  border: 2px solid #fff;
  border-radius: 50%;
  position: relative;
}
.line-second {
  display: inline-block;
  width: 6px;
  height: 7px;
  border-bottom: 2px solid #fff;
  border-left: 1px solid #fff;
  position: absolute;
  top: 1px;
  left: 6px;
}
.input-ct {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#app .input {
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1;
  height: 40px;
  border-radius: 5px;
  font-size: 16px;
  text-indent: 10px;
  vertical-align: top;
  outline: none;
}
#app .submit {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 80px;
          flex: 0 0 80px;
  width: 80px;
  height: 46px;
  font-size: 16px;
  margin-left: 10px;
  border: none;
  border-radius: 5px;
  outline: none;
  background: #0ce1f9;
  cursor: pointer;
}
#app .submit:active {
  background: #b3f320;
}
.btn-ct {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 50%;
}
#app .btns {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 60px;
          flex: 0 0 60px;
  margin-top: 10px;
  margin-right: 10px;
  height: 35px;
  border: none;
  border-radius: 3px;
  outline: none;
  cursor: pointer; 
  color: #fff;
}
#app .btn-ct .btn-bg {
  background: #27555a;
}
#app .all {
  background: #252b24;
}
#app .finish {
  background: #252b24;
}
#app .unfinish {
  background: #252b24;
}
input[type=checkbox] {
  vertical-align: middle;
  margin-right: 10px;
  cursor: pointer;
}
.event-ct {
  margin-top: 10px;
  font-size: 16px;
  color: #fff;
  width: 100%;
}
.event-ct li {
  margin-top: 10px;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}
.event-ct li .checkbox {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 20px;
          flex: 0 0 20px;
}
.event-ct li span {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  vertical-align: top;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.event-ct .delete {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 50px;
          flex: 0 0 50px;
  border-radius: 3px;
  outline: none;
}
.active {
  text-decoration: line-through;
}



